import { addFavorite, deleteFavorite } from '@/api/article'

export default {
name: "ArticlePreview",
props:{
	article:{
		type: Object,
		required: true
	}
},
methods: {
	async toggleFavorite() {
		try{
			const { favorited, slug } = this.article
			favorited ? await deleteFavorite(slug) : await addFavorite(slug)
			this.$router.go(0)
		}catch(err){
			window.alert('操作失败，请重新再试！')
		}
	},	
}
}
